import wx from "weixin-js-sdk";
import { mapState } from "vuex";
import routes from '../../routers'; // 
import store from '../../vuex/store';
import Vue from "vue";

var shareLink = store.state.path+"index.html?";//"http://www.jubenxiong.com/aml/index.html";//window.location.href;
var shareimgUrl = store.state.domainUrl+"wxapi/amxlloge.jpg";
var sharetitle = "";//"";
var shareDesc = "18";
var urlLink = store.state.path+'index.html?';

var tokens = "";

export default{

      testFun:function(){
        console.log("link="+shareLink);
      },
      defultShareMsg:function(){
        shareLink = store.state.domainUrl+'wxapi/GetUserInfo/dropindex.php?type=amlzlogin&userid='+store.state.userid;
        shareimgUrl = store.state.domainUrl+"wxapi/amxlloge.jpg";
        sharetitle = "";
        shareDesc = "18";
      },
      updateShareMsg:function(slink,simg,sdesc,stitle){
        //
        if(slink!=''){
            shareLink = slink;
        }

        if(simg!=''){
            shareimgUrl = simg;
        }

        if(sdesc!=''){
            shareDesc = sdesc;
        }

        if(stitle!=''){
            sharetitle = stitle;
        }
        this.getWxData();
      },
      getWxData:function(types=0) {
  
        var url = ''
        urlLink = store.state.path+"index.html?";
        console.log("url="+window.location.href+'urllink='+urlLink+"urlLink=="+shareLink+"desc="+shareDesc);

        Vue.prototype.$http
          .post(store.state.domainUrl+"wxapi/share.php", {
            urls: urlLink
          })
          .then(response => {
            let data = response.body;
            var wxob = {};
  
            wxob.timestamp = data.timestamp;
            wxob.nonceStr = data.nonceStr;
            wxob.signature = data.signature;
            wxob.link = urlLink;
            wxob.imgUrl = shareimgUrl;
            wxob.title = sharetitle;
            wxob.desc = shareDesc;
            this.setData(wxob, this);
            if(types == 0){
              //this.$router.replace({ name: "combineHome" });
            }
          })
          .catch(response=>{
            console.log(response);
          });
      },
      setData:function(obj, selfobj) {
        // console.log("this.shareimgUrl=" + selfobj.shareimgUrl);
        wx.config({
          debug: false,
          appId: store.state.wxappid,
          timestamp: obj.timestamp,
          nonceStr: obj.nonceStr,
          signature: obj.signature,
          jsApiList: [
            //  API 
            "onMenuShareTimeline",
            "onMenuShareAppMessage",
            "onMenuShareQQ",
            "onMenuShareWeibo",
            "onMenuShareQZone",
            "chooseImage",
            "uploadImage",
            "downloadImage",
            "chooseWXPay"
          ]
        });
        wx.ready(function() {
            console.log("shareimgUrl"+shareimgUrl);
          //  API
          wx.onMenuShareTimeline({
            title: sharetitle, // 
            
            //desc: shareDesc, // 
  
            link: shareLink,//selfobj.shareLink, // 
  
            imgUrl: shareimgUrl, // 
  
            success: function() {
              // 
              //console.log("im ok");
            },
  
            cancel: function() {
              // 
              //console.log("im fail");
            }
          });
  
          wx.onMenuShareAppMessage({
            title: sharetitle, // 
  
            desc: shareDesc, // 
  
            link: shareLink,//selfobj.shareLink, // 
  
            imgUrl: shareimgUrl, // 
  
            type: "", // ,musicvideolinklink
  
            dataUrl: "", // typemusicvideo
  
            success: function() {
              // 
            },
  
            cancel: function() {
              // 
            }
          });
  
          wx.onMenuShareQQ({
            title: sharetitle, // 
  
            desc: shareDesc, // 
  
            link: shareLink, // 
  
            imgUrl: shareimgUrl, // 
  
            success: function() {
              // 
            },
  
            cancel: function() {
              // 
            }
          });
  
          //
          wx.onMenuShareWeibo({
            title: sharetitle, // 
  
            desc: shareDesc, // 
  
            link: shareLink, // 
  
            imgUrl: shareimgUrl, // 
  
            success: function() {
              // 
            },
  
            cancel: function() {
              // 
            }
          });
  
          wx.onMenuShareQZone({
            title: sharetitle, // 
  
            desc: shareDesc, // 
  
            link: shareLink, // 
  
            imgUrl: shareimgUrl, // 
  
            success: function() {
              // 
            },
            cancel: function() {
              // 
            }
          });
  
        });
  
        wx.error(function(res) {
          //console.log("res="+res.errMsg);
          // alert("error=" + res.errMsg);
        });
      }
}


