//
function PayToRouter() {
  var buystate = window.getCookie("buyState");
  if (buystate != "paycode") return false;

  var payTypes = "";
  var payMsg = window.getCookie("payMsg");
  if (payMsg != "" && payMsg != null) {
    var payArr = payMsg.split(",");
    for (var i = 0; i < payArr.length; i++) {
      var parr = payArr[i].split(":");
      if (parr[0] == "types") {
        payTypes = parr[1];
        break;
      }
    }
  }
  if (payTypes == "GoodsPayParam") {
    //
    router.replace("/payAffirm");
    window.setCookie("buyState", "waitpayToServer");
  } else if (payTypes == "DepositPayParam") {
    //
    router.replace("/pledgeAffirm");
    window.setCookie("buyState", "waitpayToServer");
  }
  return true;
}
